import React, {useState} from 'react';
import {Link} from 'react-router-dom'

const ParentSideBarNavigation = () => {
    const [sidebar, setSidebar] = useState(false);
    const showSidebar = () => setSidebar(!sidebar);
    return (
        <nav className="py-3 navbar-light bg-light">
            <ul  className="nav flex-column">
                <li className="nav-item">
                    <Link to="/dashboard/naudotojo-duomenys" className="nav-link active">Naudotojo duomenys</Link>
                </li>
                <li className="nav-item">
                    <Link to="/dashboard/tevai" className="nav-link active">Tėvo/Globėjo pildymo forma</Link>
                </li>
                <li className="nav-item">
                    <Link to="/dashboard/vaikai" className="nav-link active">Vaikų duomenų pildymo forma</Link>
                </li>
                <li className="nav-item">
                    <Link to="/dashboard/registracija" className="nav-link active">Pildyti prašymą į darželį</Link>
                </li>
                <li className="nav-item">
                    <Link to="/dashboard/prasymai" className="nav-link active">Valdyti prašymus</Link>
                </li>
                <li className="nav-item">
                    <Link to="/dashboard/tvarka" className="nav-link active">Vaikų priėmimo tvarka</Link>
                </li>
                <li className="nav-item">
                    <Link to="/dashboard/staytistika" className="nav-link active">Statistika</Link>
                </li>
                <li className="nav-item">
                    <Link to="/dashboard/salygos" className="nav-link active">Sąlygos</Link>
                </li>
            </ul>
        </nav>
    );
};
export default ParentSideBarNavigation
